<template>
  <div style="overflow:hidden">
    <div class="containers">
      <img src="../../assets/cooperation/bg-cooperate.png" alt="">
    </div>
    <div class="cooperation">
      <div class="container">
        <v-title :title="this.$t('m.businessCooperation')"></v-title>
        <div class="row">
          <div class="wrapper" style="margin-right:330px">
            <img src="../../assets/cooperation/img-supplier.png" alt="">
            <div class="right">
              <div class="title">{{$t('m.cooperation')}}</div>
              <div class="phone">{{$t('m.contactWay')}}：02-105 4607</div>
            </div>
          </div>
          <div class="wrapper">
            <img src="../../assets/cooperation/img-logistics.png" alt="">
            <div class="right">
              <div class="title">{{$t('m.expressCooperation')}}</div>
              <div class="phone">{{$t('m.contactWay')}}：02-105 4607</div>
            </div>
          </div>
        </div>
        <div class="wrapper">
            <img src="../../assets/cooperation/img-others.png" alt="">
            <div class="right">
              <div class="title">{{$t('m.otherCooperation')}}</div>
              <div class="phone">{{$t('m.contactWay')}}：02-105 4607</div>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>
<style scoped lang='stylus'>
  @import '../../common/stylus/mixin.styl'
  .containers
    width 1200px
    margin 0 auto
    position relative
    height 438px
    img
      position absolute
      left -360px
  .cooperation
    padding 64px 0 92px 0
    .container
      .row
        margin-top 50px
        margin-bottom 62px
  .wrapper
    padding-left 14px
    display inline-block
    img
      width 100px
      height 100px
    .right
      display inline-block
      vertical-align top
      margin-left 32px
      .title
        margin-top 30px
        margin-bottom 16px
        font-family: MicrosoftYaHei;
        font-size: 14px;
        text-align: left;
        color: #333333;
      .phone
        font-family: MicrosoftYaHei;
        font-size: 14px;
        font-weight: bold;
        text-align: left;
        color: #541f7e;
</style>


